<template>
  <div id="second">
    <div id="title-box">
      <h1>travelGo行程规划</h1>
      <el-button size="medium" @click="handleBtnClick">跳转至实时规划</el-button>
    </div>
    <div style="height: 700px">
      <vue-googlemap id="googlemap" :center='center'
                     :zoom='zoom'
                     :controls='controls'>
        <vue-googlemap-marker :position='center'
                              draggable
                              events='events'
                              label="NewYork">
        </vue-googlemap-marker>
        <vue-googlemap-marker :position='[40.689249,-74.0445]'
                              draggable
                              label="A"
                              v-if="showA">
        </vue-googlemap-marker>
        <vue-googlemap-marker :position='[40.779437,-73.963244]'
                              draggable
                              label="B"
                              v-if="showB">
        </vue-googlemap-marker>
        <vue-googlemap-marker :position='[40.759011,-73.9844722]'
                              draggable
                              label="C"
                              v-if="showC">
        </vue-googlemap-marker>
        <vue-googlemap-marker :position='[40.7828647,-73.9653551]'
                              draggable
                              label="D"
                              v-if="showD">
        </vue-googlemap-marker>
        <vue-googlemap-marker :position='[40.7484405,-73.9856644]'
                              draggable
                              label="E"
                              v-if="showE">
        </vue-googlemap-marker>
        <vue-googlemap-marker :position='[36.1314583,-95.9664237]'
                              draggable
                              label="F"
                              v-if="showF">
        </vue-googlemap-marker>

        <vue-googlemap-infoWindow :position='center'
                                  draggable
                                  ref='window'
        ></vue-googlemap-infoWindow>
      </vue-googlemap>
    </div>
    <div id="checkbox">
      <h2>选择你想去的地方</h2>
      <div id="user-select">
        <el-checkbox-group v-model="checkList">
          <el-checkbox label="A" @change="changeA">Statue of Liberty</el-checkbox>
          <el-checkbox label="B" @change="changeB">The Metropolitan Museum of Art</el-checkbox>
          <el-checkbox label="C" @change="changeC">Times Square</el-checkbox>
          <br>
          <el-checkbox label="D" @change="changeD">Central Park</el-checkbox>
          <el-checkbox label="E" @change="changeE">Empire State Building</el-checkbox>
          <el-checkbox label="F" @change="changeF">Fifth Avenue</el-checkbox>
        </el-checkbox-group>
        <div id="time-select">
          <el-time-select
            placeholder="起始时间"
            v-model="startTime"
            :picker-options="{
          start: '08:30',
          step: '00:15',
          end: '18:30'
        }">
          </el-time-select>
          <el-time-select
            placeholder="结束时间"
            v-model="endTime"
            :picker-options="{
          start: '08:30',
          step: '00:15',
          end: '18:30',
          minTime: startTime
        }">
          </el-time-select>
          <el-button type="primary">submit</el-button>
        </div>

      </div>

    </div>
    <!--返回来的table-->
    <div id="table" v-if="showTable">
          <el-table>
            <el-table-column>

            </el-table-column>
          </el-table>
    </div>
  </div>

</template>
<script>
  import router from 'vue-router'
  export default {
    name: 'second',
    data(){
      return{
        center: [40.689249, -74.0445],
        zoom:11,
        controls: ['zoom', 'streetView'],
        events: {
          click(o) {
            console.log(o)
          }
        },
        checkList:[],
        startTime: '',
        endTime: '',
        showA:false,
        showB:false,
        showC:false,
        showD:false,
        showE:false,
        showF:false
      }
    },
    methods:{
      handleBtnClick(){
        this.$router.push('/')
      },
      changeA(show){
        if(this.showA){
          this.showA=false
        }else{
          this.showA=true
        }
      },
      changeB(show){
        if(this.showB){
          this.showB=false
        }else{
          this.showB=true
        }
      },
      changeC(){
        if(this.showC){
          this.showC=false
        }else{
          this.showC=true
        }
      },
      changeD(){
        if(this.showD){
          this.showD=false
        }else{
          this.showD=true
        }
      },
      changeE(){
        if(this.showE){
          this.showE=false
        }else{
          this.showE=true
        }
      },
      changeF(){
        if(this.showF){
          this.showF=false
        }else{
          this.showF=true
        }
      }

    }

  }
</script>
<style>
  #user-select{
    display: flex;
    justify-content: space-around;

  }
</style>



